import React from "react";
import { fetchGenData } from './apiCalls';
import { useState } from "react";
import { Button, ButtonGroup, Card, Row, Col, Form, OverlayTrigger, Tooltip } from "react-bootstrap";
import { useEffect } from "react";
import { Star, StarFill } from "react-bootstrap-icons";

// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getAuth, signInWithEmailAndPassword, signOut } from "firebase/auth";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
    apiKey: "AIzaSyAhrjfPhf4kj1QbtPkOgsyM-Jit8-1hCkE",
    authDomain: "cs378-p4-d3126.firebaseapp.com",
    databaseURL: "https://cs378-p4-d3126-default-rtdb.firebaseio.com",
    projectId: "cs378-p4-d3126",
    storageBucket: "cs378-p4-d3126.appspot.com",
    messagingSenderId: "201747333702",
    appId: "1:201747333702:web:dffbbadf931092f8634047",
    measurementId: "G-EB6DKGNWG0"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);

const databaseURL = "https://cs378-p4-d3126-default-rtdb.firebaseio.com";

function Home() {

    const [gen, setGen] = useState(1)
    const [pokeList, setPokeList] = useState([])
    const [search, setSearch] = useState("")
    const [genList, setGenList] = useState([])
    const [favList, setFavList] = useState([])
    const [user, setUser] = useState("")

    useEffect(() => {
        loadGen(gen, setGen)
        filterSearch(search)
        if(auth.currentUser) {
            getData()
        }
    }, [user]);

    const logInWithEmailAndPassword = async (email, password) => {
        try {
            await signInWithEmailAndPassword(auth, email, password);
        } catch (err) {
            console.error(err);
            alert(err.message);
        } finally {
            console.log("running")
            setUser(auth.currentUser.email)
            getData()
        }
    };
    const sendData = (favList) => {
        const newDict = {
            favorites: favList
        };
        return fetch(`${databaseURL + "/users/" + auth.currentUser.email.split('@')[0]}.json`, {
            method: "PUT",
            body: JSON.stringify(newDict)
        }).then((res) => {
            if (res.status !== 200) {

                throw new Error(res.statusText);
            } else {
                console.log("favorited")
                console.log(favList)
                return;
            }
        });
    };

    const getData = () => {
        fetch(`${databaseURL + "/users/" + auth.currentUser.email.split('@')[0]}.json`)
            .then((res) => {
                if (res.status !== 200) {
                    throw new Error(res.statusText);
                } else {
                    return res.json();
                }
            })
            .then((res) => {
                if (res) {
                    const keys = Object.keys(res);
                    setFavList(res.favorites)
                    // const dataPoints = keys.map((k) => res[k]);
                    // console.log(dataPoints)
                }
            });
    };

    const logout = () => {
        signOut(auth);
        setFavList([])        
    };
    function filterSearch(param) {
        const filtered = genList.filter(pokemon => (pokemon.name).includes(param))
        console.log(filtered)
        setSearch(param)
        setPokeList(filtered)
    }
    function handleFav(pokename) {
        let temp = [...favList]
        const index = temp.indexOf(pokename)
        if (index > -1) {
            temp.splice(index, 1)
        } else {
            temp.push(pokename)
        }
        if(auth.currentUser) {
            setFavList(temp)
            sendData(temp)
        }
    }
    async function loadGen(genn, setGen) {
        const temp = await fetchGenData(genn)
        setPokeList(temp)
        setGenList(temp)
        setGen(genn)
    }
    function capitalizeString(string) {
        return string.charAt(0).toUpperCase() + string.slice(1)
    }
    function formatAbilities(abilities) {
        let formatted = capitalizeString(abilities[0])
        for (let i = 1; i < abilities.length; i++) {
            formatted += " / " + capitalizeString(abilities[i])
        }
        return formatted
    }

    function FavStar(pokename) {
        if (favList.indexOf(pokename.pokename) > -1) {
            return (<StarFill className="p0 m0 star" size={30} onClick={() => handleFav(pokename.pokename)}></StarFill>)
        } else {
            return (<Star className="p0 m0 star" size={30} onClick={() => handleFav(pokename.pokename)}></Star>);
        }
    }

    return (
        <>
            <h1>Pokemon Gens I-III</h1>
            <ButtonGroup className="pb-3" aria-label="Basic example">
                <Button variant="outline-primary" active={gen === 1} onClick={() => { loadGen(1, setGen) }}>Gen I</Button>
                <Button variant="outline-primary" active={gen === 2} onClick={() => { loadGen(2, setGen) }}>Gen II</Button>
                <Button variant="outline-primary" active={gen === 3} onClick={() => { loadGen(3, setGen) }}>Gen III</Button>
            </ButtonGroup>
            <ButtonGroup className="pb-3" aria-label="User Accounts">
                <Button variant="outline-primary" active={auth.currentUser && auth.currentUser?.email === "usera@gmail.com"} onClick={() => { logInWithEmailAndPassword("usera@gmail.com", "passworda") }}>User A</Button>
                <Button variant="outline-primary" active={auth.currentUser && auth.currentUser?.email === "userb@gmail.com"} onClick={() => { logInWithEmailAndPassword("userb@gmail.com", "passwordb") }}>User B</Button>
                <Button variant="outline-primary" active={auth.currentUser && auth.currentUser?.email === "userc@gmail.com"} onClick={() => { logInWithEmailAndPassword("userc@gmail.com", "passwordc") }}>User C</Button>
                <Button variant="outline-primary" active={!auth.currentUser} onClick={() => { logout() }}>Logout</Button>
            </ButtonGroup>

            <Form>
                <Form.Group className="m-4" controlId="exampleForm.ControlInput1">
                    <Form.Label>Search By Name for Gen</Form.Label>
                    <Form.Control placeholder="search pokemon name" onChange={e => filterSearch(e.target.value)} />
                </Form.Group>
            </Form>
            <Row className="g-3 justify-content-center" xs='auto'>
                {
                    pokeList.map(pokemon => {
                        return (
                            <Col key={pokemon.name}>
                                <FavStar pokename={pokemon.name} />
                                <Card style={{ width: '18rem', height: '27rem' }}>
                                    <Card.Img variant="top" src={pokemon.sprite} />
                                    <Card.Body>
                                        <Card.Title>{(pokemon.name).charAt(0).toUpperCase() + (pokemon.name).slice(1)}</Card.Title>
                                        <Row className="justify-content-center">
                                            {
                                                pokemon.types.map(type => {
                                                    return (
                                                        <Col key={pokemon.name + type}>
                                                            <div className={"type " + type}>{type.toUpperCase()}</div>
                                                        </Col>
                                                    )
                                                })
                                            }
                                        </Row>
                                        <Card.Text className="pb-3">
                                            <strong>Abilities:</strong> {formatAbilities(pokemon.abilities)}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>
                            </Col>
                        )
                    })
                }
            </Row>
        </>
    )
}

export default Home;